.counterBody {
    border: 2px solid #e5e100;
    border-radius: 10px;
    width: 300px;
    height: 250px;
    text-align: center;
    padding: 20px 1px 1px 1px;
}

.counterNumber {
    background: #e5e100;
    width: 250px;
    height: 100px;
    margin: 1px auto 1px auto;
    border-radius: 10px;
}

.number {
    font-size: 70px;
    font-family: "Arial Black", arial-black;
}

.counterFunction {
    display: flex;
    justify-content: space-around;
    border: 2px solid #e5e100;
    border-radius: 10px;
    width: 250px;
    height: 100px;
    margin: 20px auto;
}

.inc {
    width: 80px;
    height: 40px;
    background: #e5e100;
    margin-top: 25px;
    align-content: center;
    font-size: 20px;
    font-weight: bold;
    border-radius: 10px;
}

.reset {
    width: 80px;
    height: 40px;
    background: #e5e100;
    margin-top: 25px;
    align-content: center;
    font-size: 20px;
    font-weight: bold;
    border-radius: 10px;
}

.numberError {
    color: red;
    font-size: 70px;
    font-family: "Arial Black", arial-black;
}

.counterNumber {
    display: inline-table;
}

.counterValueMax {
    display: flex;
    justify-content: space-around;
    margin-top: 20px;
}

.counterValueStart {
    display: flex;
    justify-content: space-around;
    margin-top: 20px;
}

.counterInputValue {
    width: 100px;
}